@import url(https://fonts.googleapis.com/css?family=Lato|Rajdhani:400,700&display=swap);
.card-container {
	text-align: center;
	background: white;
	display: inline-block;
	width: 20%;
	padding: 0;
	margin: 2.5%;
	-webkit-transition: all 500ms;
	transition: all 500ms;
}

.card-container:hover {
	box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
}

.card-container img {
	display: block;
	background: #eee;
	width: 100%;
	height: auto;
}

.card-container .details {
	display: flex;
	height: 5vw;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.card-container h2 {
	color: #d16666;
	font-size: 1.5vw;
	padding: 0px 10px;
}

.card-container div div {
	background: #b6c649;
	height: 4px;
}

.card-container p {
	color: white;
	padding: 5px 10px;
	font-size: 1vw;
}

.card-container div {
	background: #000;
}

.box {
	padding: 10px;
	text-align: center;
	margin: 2.5%;
}

.type-field {
	padding: 10px;
	background: #d16666;
	color: white;
	outline: none;
	border: none;
	font-size: 1.2vw;
	display: inline-block;
	width: 30%;
}

.type-field::-webkit-input-placeholder {
	color: #ccc;
}

.type-field::-moz-placeholder {
	color: #ccc;
}

.type-field:-ms-input-placeholder {
	color: #ccc;
}

.type-field::-ms-input-placeholder {
	color: #ccc;
}

.type-field::placeholder {
	color: #ccc;
}

.container {
	text-align: center;
}

h1 {
	font-size: 7vw;
	letter-spacing: 4px;
	margin: 4% 0 2.5%;
	color: #ebfc7c;
	font-family: "Rajdhani", sans-serif;
}

* {
	padding: 0;
	margin: 0;
}

body {
	margin: 0;
	padding: 0;
	font-family: "Lato", sans-serif;
	background: -webkit-gradient(linear, left top, right top, from(#cc9fc2), to(#2c4251));
	background: -webkit-linear-gradient(left, #cc9fc2 0%, #2c4251 100%);
	background: linear-gradient(to right, #cc9fc2 0%, #2c4251 100%);
}

